
import { Routes, Route } from 'react-router-dom';
import { useState } from 'react';
import { MantineProvider, ColorSchemeProvider } from '@mantine/core';
import NewsPage from './components/NewsPage';
import Trending from './components/Trending';
import { FaqWithImage } from './components/faq';
import { FeaturesCards } from './components/FeaturesCards';
import Lokalet from './components/Lokalet';
import LandingPage from './components/LandingPage';
import SanityComp from './components/SanityComp';
import CompanyPage from './components/CompanyPage';
import { PrivacyPolicy } from './components/PrivacyPolicy';
import { TermsAndContitions } from './components/TermsAndConditions';
import CompanyPageApi from './components/CompanyPageApi';

function App() {
  const [colorScheme, setColorScheme] = useState("dark")
  const toggleColorScheme = value =>
    setColorScheme(value || (colorScheme === 'dark' ? 'light' : 'dark'));
  return (
    <ColorSchemeProvider
      colorScheme={colorScheme}
      toggleColorScheme={toggleColorScheme}
    >
      <MantineProvider
        theme={{ colorScheme }}
        withGlobalStyles
        withNormalizeCSS
      >
        <div className="App">
          <Routes>
            <Route exact path="/" element={<LandingPage/>} />
            <Route exact path="/home" element={<LandingPage/>} />
            <Route exact path="/newspage" element={<NewsPage/>} />
            <Route exact path="/trending" element={<Trending/>} />
            <Route exact path="/faq" element={<FaqWithImage/> } />
            <Route exact path="/about" element={<FeaturesCards/> } />
            <Route exact path="/byen" element={<Lokalet/> } />
            <Route exact path="/sanity" element={<SanityComp/> } />
            <Route exact path="/company" element={<CompanyPage/>} />
            <Route exact path="/brreg" element={<CompanyPageApi/>} />
            <Route exact path="/privacy_policy" element={<PrivacyPolicy/>} />
            <Route exact path="/terms_conditions" element={<TermsAndContitions/>} />
          </Routes>
        </div>

      </MantineProvider>
    </ColorSchemeProvider>

  );
}

export default App;
